import React from 'react';
import styled from 'styled-components';

function Button({ name, icon, onClick, bg, bPad, color, bRad, hColor }) {
    return (
        <ButtonStyled
            style={{
                background: bg, 
                padding: bPad, 
                borderRadius: bRad,
                color: color,
                hoverColor:hColor
            }}
            onClick={onClick}
        >
            {icon}
            {name}
        </ButtonStyled>
    );
}

const ButtonStyled = styled.button`
    outline: none;
    border: none;
    font-family: inherit;
    font-size: inherit;
    display: flex;
    align-items: center;
    gap: 0.1rem;
    cursor: pointer;
    transition: all 0.4s ease-in-out;
`;

export default Button;
